@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

input {
  box-sizing: border-box;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
}

body {
  background-color: #071933;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  color: white;
}

button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

form {
  width: 100%;
}

footer {
  text-align: center;
  margin-top: 2rem;
}

.nweet-input::-webkit-input-placeholder {
  color: white;
  opacity: 0.9;
}

.nweet-input:-ms-input-placeholder {
  color: white;
  opacity: 0.9;
}

.nweet-input::placeholder {
  color: white;
  opacity: 0.9;
}

#root {
  padding: 1rem 1rem;
}

.app__container {
  width: 100%;
  max-width: 360px;
  margin: 1rem auto;
  padding: 1rem 0.5rem;
  border-radius: 25px;
  background: linear-gradient(145deg, #071b37, #06172e);
  box-shadow: 5px 5px 8px #030a14, -5px -5px 8px #0b2852;
}

.router__container {
  max-width: 890px;
  min-height: 1vh;
  width: 100%;
  margin: 2rem 0 auto;
  display: flex;
  justify-content: center;
}

.container {
  width: 100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
}

.notice {
  margin: 10px auto;
  color: rgba(255, 255, 255, 0.5);
}

.formInput {
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid black;
  text-align: center;
  background-color: white;
  color: black;
}

.formBtn {
  cursor: pointer;
  padding: 7px 20px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color: #04aaff;
  cursor: pointer;
}

.cancelBtn {
  cursor: pointer;
  background-color: tomato;
}

/* Navigation */

.nav__container {
  display: flex;
  width: 100%;
  max-width: 320px;
  justify-content: space-around;
  padding-top: 2rem;
  margin: 0 auto;
  align-items: baseline;
}

/* Profile Page */

.profileForm {
  padding-bottom: 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.logOut {
  margin-top: 50px;
}

.profile__img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 4px solid #04aaff;
  margin: 2rem auto;
}

.profile__img__container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2rem auto;
  color: #04aaff;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 4px solid #04aaff;
}

.profile__label {
  color: #04aaff;
  cursor: pointer;
  margin: 0 auto;
}

.profile__label span {
  margin-right: 5px;
}

/* Loader */

.loader__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.loading__icon {
  font-size: 3rem;
  margin: 3rem auto;
}

/* Nwitter Factory */

.factoryForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 320px;
}

.factoryInput__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
  margin-bottom: 20px;
  width: 100%;
}

.factoryInput__input {
  flex-grow: 1;
  height: 40px;
  padding: 0px 20px;
  color: white;
  border: 1px solid #04aaff;
  border-radius: 20px;
  font-weight: 500;
  font-size: 12px;
  background-color: #030a14;
}

.factoryInput__arrow {
  position: absolute;
  right: 0;
  background-color: #04aaff;
  height: 40px;
  width: 40px;
  padding: 10px 0px;
  text-align: center;
  border-radius: 20px;
  color: white;
}

.factoryInput__label {
  color: #04aaff;
  cursor: pointer;
  margin-top: 10px;
}

.factoryInput__label span {
  margin-right: 10px;
  font-size: 12px;
}

.factoryForm__attachment {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.factoryForm__attachment img {
  height: 80px;
  width: 80px;
  border-radius: 40px;
}

.factoryForm__clear {
  color: #04aaff;
  cursor: pointer;
  text-align: center;
}

.factoryForm__clear span {
  margin-right: 10px;
  font-size: 12px;
}

.form__scope {
  margin-top: 1rem;
  margin-bottom: 1rem;
  font-size: 0.8rem;
  padding-left: 20px;
}

.form__scope span {
  margin-left: 10px;
}

.scope__public {
  color: white;
  margin-left: 1rem;
}

.scope__private {
  color: gray;
  margin-left: 1rem;
}

/* Nweet */

.nweetEdit .formBtn {
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 5px;
}

.nweet__container {
  margin: 20px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 320px;
}

.nweet {
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  margin-bottom: 1.5rem;
  border: 1px solid #04aaff;
  width: 100%;
  max-width: 300px;
  padding: 20px;
  border-radius: 10px;
  /* position: relative; */
  display: flex;
  flex-direction: column;
  color: rgba(255, 255, 255, 0.8);
}

.nweet__displayName {
  font-size: 1rem;
  font-weight: bold;
  margin-bottom: 1rem;
  color: white;
}

.nweet__email {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.8rem;
  margin-left: 12px;
}

.nweet__text {
  line-height: 1.2rem;
  color: white;
}

.nweet__scope {
  color: rgba(255, 255, 255, 0.5);
  font-weight: bold;
  font-style: italic;
  font-size: 0.8rem;
  margin-bottom: 1rem;
}

.nweet__text.private {
  color: rgba(255, 255, 255, 0.5);
  font-weight: bold;
  font-style: italic;
}

.nweet img {
  width: 100%;
  margin-top: 1rem;
  border-radius: 5px;
}

.nweet__actions {
  margin-top: 1rem;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-around;
}

.nweet__actions span {
  cursor: pointer;
}

.nweet__actions span:first-child {
  margin-right: 10px;
}

.container.nweetEdit {
  margin-top: 1rem;
}

/* Auth */

.authContainer {
  display: flex;
  flex-direction: column;
  height: 70vh;
  justify-content: center;
  align-items: center;
}

.authBtns {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 320px;
}

.authBtn {
  cursor: pointer;
  border-radius: 20px;
  border: none;
  padding: 10px 0px;
  font-size: 12px;
  text-align: center;
  width: 150px;
  background: white;
  cursor: pointer;
}

/* AuthForm */

.authInput {
  max-width: 320px;
  width: 100%;
  padding: 10px;
  border-radius: 30px;
  background-color: #030a14;
  margin-bottom: 10px;
  font-size: 12px;
  color: white;
}

.authSubmit {
  text-align: center;
  background: #04aaff;
  color: white;
  margin-top: 10;
  cursor: pointer;
}

.authError {
  color: tomato;
  text-align: center;
  font-weight: 500;
  font-size: 12px;
}

.authSwitch {
  color: #04aaff;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 50px;
  display: block;
  font-size: 12px;
  text-decoration: underline;
}

/* devcarrot */

.devcarrot__container {
  display: flex;
  flex-direction: column;
  background-color: #030a14;
  border-radius: 5px;
  width: 100%;
  max-width: 320px;
}

.devcarrot__header {
  margin: 1rem;
}

.devcarrot__header h1 {
  color: white;
  font-weight: bold;
  font-size: 1.3rem;
  margin-bottom: 12px;
}

.devcarrot__header h3 {
  color: rgba(255, 255, 255, 0.8);
  font-style: italic;
}

.devcarrot__info {
  margin: 0 1rem;
  line-height: 1.4rem;
}

.devcarrot__info:last-child {
  margin-bottom: 1rem;
}

.devcarrot__info li {
  color: orangered;
}

.devcarrot__info li span {
  color: white;
}

